@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,500|Rubik:300,400,500,700&display=swap);
*{
    outline: none;
    box-sizing: border-box;
}

html, body, #root{
    min-height: 100%; 
    margin: 0;
    padding: 0;
}

#root{
    background-color: #f9f9f9;
    font-family: 'Roboto', sans-serif;
    font-size: 1em;
    font-weight: 300; /* light */
}

a{
    text-decoration: none;
}

strong{
    font-weight: 500;
}
#App{
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
    width: 90%;
    max-width: 1500px;
    margin: 0 auto;
}

header{
    padding: 1em 0;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    width: 100%;
}

header img{
    box-sizing: border-box;
    width: 7em;
    height: 7em;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border: 1px solid #000000;
    border-radius: 50%;
    margin-right: 1em;
}

header h1{
    font-family: 'Rubik', sans-serif;
    font-size: 1.953em;
    font-weight: bold;
    text-align: center;
    margin-top: 0.5em;
    margin-bottom: 0;
}

header h2{
    font-family: 'Roboto', sans-serif;
    font-size: 1.563em;
    font-weight: 100; /* thin */
    text-align: center;
    margin: 0;
}

header .social-links{
    display: flex;
    justify-content: center;
    margin-top: 5px;
}

header .social-links .social-media{
    margin-right: 5px;
}

section{
    width: 100%;
}

section h3{
    font-family: 'Rubik', sans-serif;
    font-size: 1.25em;
    font-weight: 500; /* medium */
    border-bottom: 1px solid rgba(0, 0, 0, 0.25);
}

.projects{
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 2em;
}

@media (min-width: 600px){
    header{
        justify-content: flex-start;
        padding-top: 3em;
    }
    header h1, header h2{
        text-align: left;
    }
    header .social-links{
        justify-content: flex-start;
    }
    .projects{
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (min-width: 900px){
    .projects{
        grid-template-columns: repeat(3, 1fr);
    }
}
.project-card{
    border-radius: 7px;
    box-shadow: 1px 3px 10px rgba(0, 0, 0, 0.25);
    background-color: #ffffff;
}

.project-image{
    width: 100%;
    border-radius: 7px 7px 0 0 ;
}

.project-card h4{
    font-family: 'Roboto', sans-serif;
    font-size: 1.563em;
    font-weight: bold;
    margin: 0.5em 0.5em 1em 0.5em;
}

.project-card a{
    color: #000000;
}

.project-card p{
    /* margin-top: 0.5em; */
    margin-right: 0.5em; 
    margin-bottom: 1em;
    margin-left: 0.7em;
}

.project-description{
    font-family: 'Roboto', sans-serif;
    font-size: 1em;
    font-weight: 300; /* light */
}

.project-description a{
    font-weight: 400;
}

.project-language{
    font-family: 'Roboto', sans-serif;
    font-weight: 100; /* thin */
    margin-top: auto;
}

.project-repo{
    display: flex;
    align-items: center;
}

.project-repo .icon{
    margin-right: 0.5rem;
}
